@use '../util' as *;

.slideshow {
    position: relative;
    &__slides {
        position: relative;
        height: 60vh;
        padding: 0px;
        margin: 0px;
        list-style-type: none;
        .slide {
            position: absolute;
            left: 0px;
            top: 0px;
            width: 100%;
            height: 100%;
            opacity: 0;
            z-index: 1;
            -webkit-transition: opacity 1s;
            -moz-transition: opacity 1s;
            -o-transition: opacity 1s;
            transition: opacity 1s;
        }
        .active {
            opacity: 1;
            z-index: 2;
        }
        .slide:nth-of-type(1){
            background-image: url('/media/images/img1.jpg');
            background-size: cover;
            background-position: 50% 50%;
            background-repeat: no-repeat;
        }
          
        .slide:nth-of-type(2){
            background-image: url('/media/images/img2.jpg');
            background-size: cover;
            background-position: 50% 50%;
            background-repeat: no-repeat
        }
          
        .slide:nth-of-type(3){
            background-image: url('/media/images/img3.jpg');
            background-size: cover;
            background-position: 50% 50%;
            background-repeat: no-repeat
        }
    }
    &__controlls {
        height: inherit;
        &__arrows {
            position: absolute;
            width: 100%;
            height: 20%;
            top: 50%;
            transform: translateY(-50%);
            z-index: 30;
            .arrow {
                position: absolute;
                padding: 3rem;
                box-shadow: rem(1) rem(-1) 0 rem(1) var(--text-color) inset;
                -webkit-box-shadow: rem(2) rem(-2) var(--text-color) inset;
                border: solid transparent;
                border-width: 0 0 2rem 2rem;
                transition: 0.2s;
                &.previous {
                    transform: rotate(45deg);
                    left: 4%
                }
                &.next {
                    transform: rotate(225deg);
                    right: 4%
                }
            }
            .arrow:hover {
                cursor: pointer;
                box-shadow: 2px -2px 0 2px var(--text-color) inset;
                -webkit-box-shadow: 4px -4px var(--text-color) inset;
            }
            .next:active {
                right: 5%;
            }
            .previous:active {
                left: 5%;
            }
        }
        &__autoplay {
            display: flex;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 50px;
            height: 50px;
            z-index: 25;
            .autoplay {
                margin: auto;
            }
            .autoplay:hover {
                cursor: pointer;
            }
            .playing:before {
                content: "\f28b";
                font-size: rem(24);
                font-family: "Font Awesome 5 Free";
                font-weight: bold;
            }
            .playing:active::before {
                font-size: rem(28);
            }
            .paused:before {
                content: "\f144";
                font-size: rem(24);
                font-family: "Font Awesome 5 Free";
                font-weight: bold;
            }
            .paused:active::before {
                font-size: rem(28);
            }
        }
        &__indicators {
            z-index: 10;
             ul {
                list-style: none;
                display: flex;
                position: absolute;
                bottom: 0;
                left: 50%;
                margin: 0;
                padding: 0;
                transform: translate(-50%);
                z-index: inherit;
                .indicator {
                    width: 10px;
                    height: 10px;
                    background: var(--light-bg);
                    border: 2px solid var(--light-bg);
                    border-radius: 50px;
                    margin: 10px;
                    cursor: pointer;
                }
                .active {
                    background: transparent !important;
                }
            }
        }
    }
}