  align-items: center;
  overflow: hidden;
}

.header:hover {
  background: var(--background);
  transition: 0s;
}

.sticky {
  background: var(--background);
  top: -3.125rem;
}

body.light #theme-switch {
  transform: translateX(1.625rem);
}
body.light #switch-bg::before {
  content: "";
  position: absolute;
  right: calc(100% - 20px);
  display: inline-block;
  height: 1rem;
  width: 1rem;
  border-radius: 50%;
  background-color: #262626;
  transition: 0.2s ease-out;
}
body.light #switch-bg::after {
  content: "";
  position: absolute;
  right: calc(100% - 27px);
  top: -1px;
  display: inline-block;
  height: 1rem;
  width: 1rem;
  border-radius: 50%;
  background-color: #fff;
  transition: 0.2s ease-out;
}
body.dark #theme-switch {
  transform: translateX(-0.0625rem);
}
body.dark #switch-bg::before {
  content: "";
  position: absolute;
  right: 6px;
  width: 14px;
  height: 14px;
  background-color: #ffd712;
  filter: blur(4px);
  transition: 0.2s ease-out;
}
body.dark #switch-bg::after {
  content: "";
  position: absolute;
  background-color: #ffd712;
  width: 12px;
  height: 12px;
  top: initial;
  right: 8px;
  border-radius: 50%;
  transition: 0.2s ease-out;
}

.section__wrapper {
