}
.slideshow__slides .slide {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1;
  -webkit-transition: opacity 1s;
  -moz-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
}
.slideshow__slides .active {
  opacity: 1;
  z-index: 2;
}
.slideshow__slides .slide:nth-of-type(1) {
  background-image: url("/media/images/img1.jpg");
