}
.slideshow__controlls__arrows .arrow:hover {
  cursor: pointer;
  box-shadow: 2px -2px 0 2px var(--text-color) inset;
  -webkit-box-shadow: 4px -4px var(--text-color) inset;
}
.slideshow__controlls__arrows .next:active {
